@use '../../styles/partials/colors';
@use '../../styles/partials/mixins';

.contact {
    width: 100%;
    background-color: colors.$background-pink;

    &__title { 
        text-align: center ;
        font-size: 2rem;
        padding: 3rem 0 0.5rem 0;
        margin: 4rem auto;
        @include mixins.tablet {
            font-size: 3rem;
            margin-bottom: 2rem;
        }
        @include mixins.largeDesktop {
            margin: 6rem auto;
        }
    }

    &__form {
        margin: 1rem 3rem 0 3rem;
        @include mixins.tablet {
            margin: 1rem 7rem 0 7rem;
        }
        @include mixins.desktop {
            width: 50%;
            margin: 1rem 0 0 4rem;
        }
    }
    &__form-box {
        @include mixins.desktop {
            display: flex;
            justify-content: center;
        }
    }
    &__input {
        padding: 0.5rem;
        border-radius: 0.4rem;
        margin: 0.5rem 1rem 1rem 0;
        width: 95%;
        font-family: inherit;
        font-size: inherit;
    }
    &__button {
        background-color: colors.$primary-brown;
        color: colors.$primary-white;
        border-radius: 0.25rem;
        padding: 0.5rem 1rem;
        border: 0.05rem solid colors.$primary-brown ;
        font-family: inherit;
        font-size: 0.9rem;
        font-weight: 700;
        @include mixins.tablet {
            font-size: 1rem;
        }
        @include mixins.desktop {
            font-size: 1.3rem;
        }
        
    }
    &__button-box {
        padding: 0.5rem 0 2rem 0;
        @include mixins.tablet {
            display: flex;
            justify-content: center;
        }
    }
    &__message-box{
        resize: none;
        @include mixins.tablet {
            height: 8rem ;
        }
    }
    &__name,
    &__email,
    &__customer,
    &__message {
        font-size: 1.2rem;
        @include mixins.desktop {
            font-size: 1.3rem;
        }
    }
    &__image {
        display: none;
        @include mixins.desktop {
            display: inline-flex;
            width: 30rem;
            height: 30rem;
        }
    }
}